<docs>
# KoddeinForm

Base form - handle data, validation and ajax requests

@author Nicolas Husson <nicolas@kffein.com> <br />
@author Kevin Bastien <kevin@kffein.com>
</docs>

<script lang="ts">
import { defineComponent, nextTick, PropType, provide, reactive, ref, toRefs, watch, readonly, /*defineEmits*/ } from 'vue';
import { assign, cloneDeep, each, first, flatten, isEmpty, keys, map, mapKeys, values, without, zipObject } from 'lodash';
import { AxiosRequestConfig, Method } from 'axios';
import { useReCaptcha } from 'vue-recaptcha-v3';
import { until, templateRef } from '@vueuse/core';
import { client, getEnv, motion } from 'config';
import { serialize } from 'object-to-formdata';
import { gsap, ScrollTrigger } from 'vendors/gsap';
import scrollStore from 'components/SmoothScroll/store';
import { store as flashStore } from 'components/UiKit/FlashMessage/store';
import { store as i18nStore } from 'plugins/i18n/store';
import ApiHelper from 'plugins/api';
import i18n from 'plugins/i18n';
import emitter, { EVENTS } from 'plugins/store/events';
import Submit from './SubmitUiButton.vue';
import SubmitIcon from './Submit.vue';
import { FORM_STATUS } from './utils';
import { useI18n } from 'vue-i18n';

interface Data {
  submit?: any,
  next?: any,
  prev?: any,
}

export interface ErrorObject {
  [key: string]: any,
}


const KoddeinForm = defineComponent({
  name: "KoddeinForm",
  components: {
    Submit,
    SubmitIcon,
  },
  props: {
    /** default theme */
    theme: {
      type: String as PropType<FormTheme>,
      default: 'default',
    },
    /** if form is splitted in multi steps */
    steps: {
      type: Number,
      default: 1,
    },
    /** form action - if defined, be sure that this contain a valid route */
    action: {
      type: String,
      default: '',
    },
    /** form method */
    method: {
      type: String as PropType<Method>,
      default: 'post',
    },
    /** form data model - from our utils `createFormModel()` */
    model: {
      type: Object,
      default: undefined,
    },
    /** list of usefull labels id needed */
    labels: {
      type: Object as PropType<{
        submit?: string,
        next?: string,
        prev?: string,
        success?: string,
        error?: string,
      }>,
      default: null,
    },
    /** list of usefull icons if needed */
    icons: {
      type: Object as PropType<{
        submit?: any,
        next?: any,
        prev?: any,
      }>,
      default: null,
    },
    /** easy way to override the send method
     * @returns {Promise} that return your response
     */
    onSend: {
      type: Function,
      default: undefined,
    },
    /** some debugs - bypas some methodes {validation} */
    debug: {
      type: Object,
      default: undefined,
    },
    /** invisible recaptcha - only if needed and settings are activated */
    recaptcha: {
      type: Boolean,
      default: true,
    },
    /** display flash Message - if true form will call UiFlashMessage for error/success state */
    flash: {
      type: Boolean,
      default: true,
    },
    /** after succes submit, rreset the complete form */
    reset: {
      type: Boolean,
      default: true,
    },
    /** display intro anim on scroll */
    animated: {
      type: Boolean,
      default: false,
    },
    /** display Full ui button with icon display */
    submitIcon: {
      type: Boolean,
      default: false,
    }
  },
  emits: ['update', 'status', 'changeStep', 'errorValidation', 'success', 'error'],
  setup(props, {emit}) {
    const {t} = useI18n()
    const { scrollable } = toRefs(scrollStore.state);

    // global ref, will be passed to children cpn. -> see provide/inject
    const el = templateRef<HTMLElement | null>('el', null)

    let currentStep = ref(1);
    let previousStep = ref(1);
    let status = ref(FORM_STATUS.PRISTINE);
    let locked = ref(false);
    let errors = ref({} as ErrorObject);
    const visible = ref(!props.animated)

    const data: Data = reactive(
      {
        submit: undefined,
        next: undefined,
        prev: undefined,
      }
    );
    const recaptchaKey = client && getEnv('VITE_RECAPTCHA_ENABLED') && getEnv('VITE_RECAPTCHA_CLIENT_KEY') ? getEnv('VITE_RECAPTCHA_CLIENT_KEY') : null;

    /**
     * Watch errors, on error -> update model
     */
    watch(
      errors,
      (errors) => {
        const model = watchErrors(errors);
        emit('update', model);
      },
      // { deep:true }
    )
    /**
     * Watch status, on status update -> send status to parent
     */
    watch(
      status,
      (value) => {
        emit('status', value as FormStatusType);
      },
      { immediate: true }
    )
    /**
     * Watch currentStep, on step update -> send new step to parent
     */
    watch(
      currentStep,
      (value) => {
        nextTick(() => {
          changeStepAnim(value, data, props.steps);
          emit('changeStep');
          displayVisibleField();
        })
      },
      { immediate: true }
    )


    // RECAPTCHA THINGY
    let recaptcha = () => { };
    if (recaptchaKey) {
      const { executeRecaptcha, recaptchaLoaded }: any = useReCaptcha(); // classic third party typing error :beer:
      recaptcha = async () => {
        // (optional) Wait until recaptcha has been loaded.
        await recaptchaLoaded()

        // Execute reCAPTCHA with action "login".
        const token = await executeRecaptcha('form')

        // Do stuff with the received token.
        send(token);
      }
    }
    // FORM METHODS ACTION
    const pristine = () => {
      errors.value = {};
      status.value = FORM_STATUS.DEFAULT;
    };

    /**
     * Reset form, every state/value/errors
     */
    const reset = () => {
      errors.value = {};
      status.value = FORM_STATUS.PRISTINE;
      const newModel = assign({}, props.model);
      each(newModel, (data) => {
        data.value = null;
      });
      previousStep.value = currentStep.value;
      currentStep.value = 1;

      emit('update', newModel);
    };

    const executeRecaptcha = () => {
      if (recaptchaKey) {
        const { executeRecaptcha, recaptchaLoaded }: any = useReCaptcha(); // classic third party typing error :beer:
        return recaptcha = async () => {
          // (optional) Wait until recaptcha has been loaded.
          await recaptchaLoaded()

          // Execute reCAPTCHA with action "login".
          const token = await executeRecaptcha('form')

          // Do stuff with the received token.
          return token;
        }
      }

      return false;
    }

    const resetSteps = () => { //eslint-disable-line @typescript-eslint/no-unused-vars
      previousStep.value = currentStep.value;
      currentStep.value = 1;
    };

    /** 
     * FORM STEPS - PREV
     */
    const prev = () => {
      if (currentStep.value > 1) {
        pristine();
        previousStep.value = currentStep.value;
        currentStep.value -= 1;
      }
    };

    /** 
     * FORM STEPS - NEXT
     */
    const next = () => {
      if (props.steps && props.steps > 1 && currentStep.value < props.steps && validate(false, props.debug?.validation)) {
        pristine();
        previousStep.value = currentStep.value;
        currentStep.value += 1;
      }
    };

    // WATCH
    const watchErrors = (errors: ErrorObject) => {
      const newModel = assign({}, props.model);
      mapKeys(errors, (fieldErrors, key) => {
        if (fieldErrors.length) {
          let error = first(fieldErrors);
          if (error) {
            const firsterror: any = first(fieldErrors);
            if (typeof firsterror === 'string') {
              const errorI18n = `FORMS.errors.${firsterror}`;
              const errorI18nTranslated = i18n.global.t ? i18n.global.t(errorI18n) : '';
              if (errorI18nTranslated !== '' && errorI18n !== errorI18nTranslated) {
                error = errorI18nTranslated;
              }
            } else {
              const errorI18n = `FORMS.errors.${firsterror.label}`;
              const errorI18nTranslated = i18n.global.t ? i18n.global.t(errorI18n, firsterror.attr) : '';
              if (errorI18nTranslated !== '' && errorI18n !== errorI18nTranslated) {
                error = errorI18nTranslated;
              }
            }
          }
          newModel[key].error = error;
        } else {
          newModel[key].error = '';
        }
      });
      return newModel;
    }

    /* FORM VALIDATION
    * Validate form, only visible field without all flag
    */
    const validate = (all = false, debug = false) => {
      if (debug) {
        return true;
      }

      // create an object like {email: [list of errors], password: [if empty = success]}
      const dataModel = cloneDeep(props.model);

      if (!all) {
        // filter data model to validate only visible fields - very usefull for paginated form
        each(dataModel, (entry, name) => {
          if (el.value?.querySelector(`[data-name='${name}']`) === null && dataModel && dataModel[name]) {
            delete dataModel[name];
          }
        });
      }

      const computeErrors = zipObject(keys(dataModel), map(dataModel, (prop) => {  
        return without(map(prop.validations, (rule) => rule(prop.value, prop, dataModel)), null, undefined, true);
      }));
      errors.value = computeErrors;

      const isSuccess = flatten(values(computeErrors)).length <= 0;

      if (!isSuccess) {
        nextTick(() => {
          status.value = FORM_STATUS.INVALID;
        })
      }

      // return true if no error
      return isSuccess;
    }

    // ANIMATION LAYOUT
    const changeStepAnim = (value: number, data: Data, steps?: number) => {
      if (props.steps === 1) return;
      
      const tl = gsap.timeline({
        onStart: () => {
          locked.value = true;
        },
        onComplete: () => {
          locked.value = false;
          emitter.emit(EVENTS.CURSOR_UPDATE);
        },
      });

      const submit = data.submit?.$el;
      const next = data.next?.$el;
      const prev = data.prev?.$el;

      if (value === 1) {
        prev && tl.to(prev, 0.2, { opacity: 0, ease: 'power1.easeOut' }, '0');
        submit && tl.to(submit,0.2, { opacity: 0, ease: 'power1.easeOut' }, '0');
        next && tl.to(next, 0.2, { opacity: 0, ease: 'power1.easeOut' }, '0');
        next && tl.to(next, 0.2, { opacity: 1, ease: 'power1.easeOut' }, '1.4');
      } else if (steps && value === steps) {
        next && tl.to(next, 0.2, { opacity: 0, ease: 'power1.easeOut' }, '0');
        prev && tl.to(prev, 0.2, { opacity: 0, ease: 'power1.easeOut' }, '0');
        prev && tl.to(prev, 0.2, { opacity: 1, ease: 'power1.easeOut' }, '1.4');
        submit && tl.to(submit, 0.2, { opacity: 1, ease: 'power1.easeOut' }, '1.4');
      } else {
        submit && tl.to(submit, 0.2, { opacity: 0, ease: 'power1.easeOut' }, '0');
        next && tl.to(next, 0.2, { opacity: 0, ease: 'power1.easeOut' }, '0');
        next && tl.to(next, 0.2, { opacity: 1, ease: 'power1.easeOut' }, '1.4');
        prev && tl.to(prev, 0.2, { opacity: 0, ease: 'power1.easeOut' }, '0');
        prev && tl.to(prev, 0.2, { opacity: 1, ease: 'power1.easeOut' }, '1.4');
      }
    }

    /**
     * on submit form
     * - does not happen when ajax call is already in progress
     * - validate form
     * - if valid : send
     */
    const submit = () => {
      if (status.value === FORM_STATUS.SUCCESS || status.value === FORM_STATUS.SENDING) {
        return;
      }

      errors.value = {};
      status.value = FORM_STATUS.DEFAULT;

      if (validate(true, props.debug?.validation)) {
        // if recaptcha execute check else normal flow
        if (props.recaptcha && recaptchaKey) {
          recaptcha()
        } else {
          send();
        }
      } else {
        emit('errorValidation');
      }
    };

    /**
     * send form
     * - if added action, this may be automatic
     * - or you can override this method by setting `onSend` prop
     */
    const send = (token?: string) => {
      return new Promise((resolve, reject) => {
        if (!props.action && !props.onSend) {
          reject(new Error('Form send without action. either add action or override this `send` method'));
        }
        status.value = FORM_STATUS.SENDING;
        // if defined, use onSend prop - must return the ajax promise
        if (props.onSend) {
          return resolve(props.onSend(token));
        }

        // auto send
        if (props.action) {
          const datas = zipObject(keys(props.model), map(props.model, ({ value }) => value));
          const options: AxiosRequestConfig = assign({}, {
            data: serialize(datas),
            method: props.method || 'post',
            url: props.action,
            headers: {},
            showErrorMessage: props.flash
          });

          // add recaptcha token if present
          if (token && props.recaptcha && options.headers) {
            options.headers['recaptcha-token'] = token;
          }

          return resolve(
            ApiHelper.request(options),
          );
        }

        return reject(new Error('@todo Ui/Form : auto send if action prop'));
      })
        // success
        .then((response) => {
          status.value = FORM_STATUS.SUCCESS;
          emit('success', response);

          if (props.flash) {
            flashStore.ADD({
              status: 'success',
              title: (props.labels && props.labels.success) ? props.labels.success : i18n.global.t('FORMS.labels.success'),
              important: false,
              duration: 20000,
            });
          }

          if(props.reset) {
            setTimeout(() => {
              reset();
            }, 6000);
          } else {
            status.value = FORM_STATUS.PRISTINE;
          }

          return response;
        })
        // error
        .catch((error: any) => {
          if (process.env.NODE_ENV === 'development') {
            console.debug('[form] onsend - catch error', { error });  
          }
          if (error.message === 'just-ignore-this') return;
          const { response } = error;

          // retrieve error message (may be translated etc)
          const message = (() => {
            const msg = response?.data?.data?.message;
            return msg && i18n.global.te(`FORMS.errors.${msg}`, i18nStore.locale?.i18n)
              ? i18n.global.t(`FORMS.errors.${msg}`)
              : i18n.global.t('FORMS.errors.generic');
          })();

          // maybe we also have some field errors
          const fieldErrors = response?.data?.meta?.errors ?? {};
          const formatMessage = (errors: any) => {
            let fieldError: any = first(errors);

            // all require is the same msg
            if (fieldError.search('ne peut Ãªtre vide') >= 0
              || fieldError.search('is required') >= 0
              || fieldError.search('required') >= 0) {
              return i18n.global.t('FORMS.errors.required');
            }

            // i18n msg cant contain `.`
            const translatedKey = `FORMS.errors.${fieldError.replace(/\./g, '')}`;
            if (i18n.global.te(translatedKey, i18nStore.locale?.i18n)) {
              fieldError = i18n.global.t(translatedKey);
            }
            return fieldError;
          };

          const model = cloneDeep(props.model);
          each(fieldErrors, (errors, key) => {
            if (!model || !model[key]) return;
            model[key].error = formatMessage(errors);
          });

          if (!isEmpty(fieldErrors)) {
            emit('update', model);
          }

          status.value = FORM_STATUS.ERROR;
          errors.value['generic'] = message;

          emit('error', message);

          setTimeout(() => {
            pristine();
          }, 6000);
        });
    };


    const displayVisibleField = async () => {
      if (motion && props.animated) {
        await until(scrollable).toBe(true)
        nextTick(() => {
          const elems = el.value?.querySelectorAll('.koddein-field, .actions');
          const st = ScrollTrigger.create({
            // markers: true,
            trigger: el.value,
            start: 'top bottom',
            end: 'bottom top',
            onToggle: ({ isActive }: { isActive: boolean }) => {
              visible.value = isActive
              if (isActive) {
                if (elems)
                  gsap.to(elems, { opacity: 1, stagger: 0.08, duration: 0.4, delay: 0.3 })
                nextTick(() => { st.kill() }) // only 1time ?
              }
            }
        })
        })
      }
    };

    const updateStatus = () => {
      if (status.value === FORM_STATUS.PRISTINE) {
        status.value = FORM_STATUS.DEFAULT;
      }
    }

    // render first view
    displayVisibleField();

    // provide data to children
    provide('theme', props.theme)
    provide('currentStep', currentStep)
    provide('previousStep', previousStep)
    // provide('status', status)
    provide('status', { value: readonly(status), update: updateStatus });
    provide('errors', errors)

    return {
      t,
      ...toRefs(data), //data
      visible,
      motion,
      // ref
      currentStep,
      previousStep,
      status,
      locked,
      errors,
      //
      FORM_STATUS,
      onNext: next,
      onPrev: prev,
      onSubmit: submit,
      executeRecaptcha,
      updateStatus,
    };
  }
});
export default KoddeinForm
</script>

<template>
  <form ref="el"
        :method="method"
        :action="action"
        :disabled="status === FORM_STATUS.SENDING"
        :data-status="status"
        :data-theme="theme"
        class="koddein-form"
        novalidate
        :data-visible="visible"
        :data-animated="animated && motion"
        @submit.prevent="onSubmit"
  >
    <div class="wrapper">
      <!-- fields / steps -->
      <slot />
    </div>

    <!-- submit -->
    <slot name="actions">
      <div :class="['actions']">
        <ui-button v-if="steps && steps > 1"
                   ref="prev"
                   tag="span"
                   :class="['button -prev', currentStep > 1 ? '-active' : '']"
                   theme="secondary"
                   :label="labels && labels.prev ? labels.prev : t('FORMS.labels.prev')"
                   tabindex="0"
                   :icon="icons && icons.prev ? icons.prev : undefined"
                   @click.prevent="() => { !locked ? onPrev() : null }"
        />
        <ui-button v-if="steps && steps > 1"
                   ref="next"
                   tag="span"
                   :class="['button -next', currentStep < steps ? '-active' : '']"
                   theme="secondary"
                   :label="labels && labels.next ? labels.next : t('FORMS.labels.next')"
                   :icon="icons && icons.next ? icons.next : undefined"
                   @click.prevent="() => { !locked ? onNext() : null }"
        />
        <SubmitIcon v-if="submitIcon"
                    ref="submit"
                    :class="['button -submit', steps && currentStep === steps ? '-active' : '']"
                    :label="labels && labels.submit ? labels.submit : t('FORMS.labels.submit')"
                    :icon="icons && icons.submit ? icons.submit : undefined"
                    :state="status"
        />
        <Submit v-else
                ref="submit"
                :class="['button -submit', steps && currentStep === steps ? '-active' : '']"
                :label="labels && labels.submit ? labels.submit : t('FORMS.labels.submit')"
                :icon="icons && icons.submit ? icons.submit : undefined"
                :state="status"
        />
      </div>
    </slot>

    <!-- list of generic (api) success -->
    <slot v-if="!flash" name="success">
      <p v-if="status === FORM_STATUS.SUCCESS"
         class="success"
         v-text="(labels && labels.success) || t('FORMS.labels.success')"
      />
    </slot>

    <!-- list of generic (api) errors -->
    <slot v-if="!flash" name="errors">
      <p v-if="errors && errors.generic"
         class="errors -generic"
         v-html="(labels && labels.error) || errors.generic"
      />
    </slot>
  </form>
</template>

<style lang="stylus" scoped>
  /**
   * TABLE OF CONTENT
   * --------
   *  - LAYOUT
   *  - ANIMATION
   *  - DEBUG
   *  - NO_SCOPE
   */

  $c-f-error ?= red

  //  ===LAYOUT====
  .wrapper
    position relative
  
  [data-animated="true"]
    .actions
      opacity 0

    :deep()
      .koddein-field
        opacity 0

  .errors
    rp(font-size, 13px 12px)
    color $c-f-error

    &.-generic
      margin-top 40px
  
  .actions
    .button
      pointer-events none
      rp(margin-right, 20px 10px)
      &:last-child
        margin-right 0 
      &.-active
        pointer-events all


  //  ===DEBUG===
  [data-debug-mode="true"] .KoddeinForm
    //
</style>
