<docs>
  # KoddeinSubmit
  > already skinned, and take some states (loading, failed, success...)
  
  @author Nicolas Husson <nicolas@kffein.com>
</docs>

<script lang="ts">
import { defineComponent, inject } from 'vue';
import IconSubmit from './assets/svg/submit.svg?raw';
import IconSending from './assets/svg/sending.svg?raw';
import IconSuccess from './assets/svg/success.svg?raw';
import IconError from './assets/svg/error.svg?raw';
import { FORM_STATUS } from './utils';
import i18n from 'plugins/i18n';

const KoddeinSubmit = defineComponent({
  name: "KoddeinSubmit",
  props: {
    ariaLabel: {
      type: String,
      default: i18n.global.t('FORMS.standard.submit button'),
    },
    label: {
      type: String,
      default: i18n.global.t('FORMS.standard.submit'),
    },
  },
  setup() {
    const status: any = inject('status');
    return {
      status,
      FORM_STATUS,
      IconSubmit,
      IconSending,
      IconSuccess,
      IconError,
    }
  },
});
export default KoddeinSubmit
</script>

<template>
  <button :data-state="status.value.value"
          type="submit"
          class="KoddeinSubmit"
          :aria-label="ariaLabel"
  >
    <span class="wrapper">
      <transition name="fade">
        <i v-if="status.value.value === FORM_STATUS.SENDING"
           class="icon -sending"
           v-html="IconSending"
        />
      </transition>
      <transition name="fade">
        <i v-if="status.value.value === FORM_STATUS.SUCCESS"
           class="icon -success"
           v-html="IconSuccess"
        />
      </transition>
      <transition name="fade">
        <i v-if="status.value.value === FORM_STATUS.ERROR"
           class="icon -error"
           v-html="IconError"
        />
      </transition>
      <span class="text" v-text="label" />
      <i :class="`icon -submit ${status.value.value === FORM_STATUS.PRISTINE || status.value.value === FORM_STATUS.DEFAULT || status.value.value === FORM_STATUS.INVALID ? '' : '-hidden'}`"
         v-html="IconSubmit"
      />
    </span>
  </button>
</template>

